* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
ul {
  list-style: none;
}
a {
  text-decoration: none;
  color: black;
}
body {
  background: #eee;
}
header > form {
  margin: 20px;
  display: flex;
  justify-content: space-between;
  max-width:400px;
}
@media (min-width:500px){
  header>form{
    margin-left:auto;
    margin-right:auto;
  }
}
header > form > button {
  background: #0282b9;
  border: none;
  border-radius: 4px;
  color: white;
  padding: 5px 10px;
  font-size: 16px;
}
header > form > input {
  flex-grow: 1;
  margin-right: 10px;
  border-radius: 4px;
  border: 1px solid #ddd;
}
main {
  margin: 20px;
}
@media (min-width:500px){
  main{
    max-width:900px;
    margin-left:auto;
    margin-right:auto;
  }
}
.site {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
}
@media (min-width:500px){
  .site{
    margin-right:-25px;
    justify-content: flex-start;
  }
}
.site > li {
  width: 160px;
  height: 160px;
  background:white;
  border-radius: 4px;
  
  margin-bottom: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  position:relative;
}
@media (min-width:500px){
  .site>li{
    margin-right:25px;
  }
}
.a,.addbutton>div {
  height: 110px;
  width: 110px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.deleteIcon{
  position:absolute;
  top:0px;
  right:0px;
}
@media(min-width:500px){
  .deleteIcon{
    display:none;
  }
  .sitelist:hover .deleteIcon{
    display:block;
  }
}
.logo {
  font-size: 56px;
}
.url {
  max-width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
}
.icon {
  width: 100%;
  height: 30px;
}
img {
  max-width: 100%;
}
